.pokehub-container{
    background: rgb(155, 5, 50);
    margin-left: 15%;
    width: 70%;
    min-height: 475px;
    display: flex;
    flex-wrap: wrap;
    border-radius: 12px;
}

.pokelist-container{
width: 300px;
height: 455px;
overflow-y: scroll;
background-color: rgba(250, 116, 33, 0.6) ;
margin: 10px;
flex-grow: 3;
border-radius: 10px 1px 1px 10px;
}

.pokesearchresult-container{
    display: flex;
    align-items: center;
    width: 300px;
    flex-grow: 1;
    margin: 10px;
    background-color: rgba(250, 116, 33, 0.6) ;
    height: 455px;
    border-radius: 1px 10px 10px 1px;
}